import React, { Component } from 'react';
import { SliderStyled, SliderItem} from './general/style/style';
import { FullWidthBtn } from './general/style/common';
//import { toJS } from 'mobx';
import { DhProperties} from '../DhProperties';
import NoSliderComp from './NoSliderComp';
import styled from 'styled-components';

export default class Slider extends Component {

  constructor(props){
    super(props);
    this.state = {
      isCarouselSupported:true
    }
  }

  async componentDidMount() {
    let tmpIsCarouselSupported = true;

    try{
      if(typeof(isCarouselSupported)!="undefined" && !isCarouselSupported()){
        tmpIsCarouselSupported = false;
      }
    }catch(err){
      console.log(err);
    }

    if(!tmpIsCarouselSupported){
      this.setState({...this.state, isCarouselSupported:tmpIsCarouselSupported});
    }


    if(typeof(this.props.onload)!='undefined'){
      this.props.onload();
    }
  }

   onViewAllClick = () => {
    if(typeof(newsHuntAction)!='undefined' && typeof(callEvent)!="undefined" && ((!isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION)) || (isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION_IOS))) && this.props.viewAllUrl && this.props.viewAllTitle){
      const url= DhProperties.properties.pwaBaseUrl+"/webItem?webModel="+encodeURIComponent(JSON.stringify({title:this.props.viewAllTitle,url:DhProperties.properties.baseUrl +`/${this.props.viewAllUrl}`}));
      callEvent(url);
    } else if(this.props.viewAllUrl){ 
     window.location.href= DhProperties.properties.baseUrl +`/${this.props.viewAllUrl}`;
    }
  }

  render() {

    let className = this.props.className || "" + " theme-SliderItem";
    className = className.trim();

    let NoSliderClassName = this.props.NoSliderClassName|| this.props.className || "" + " theme-SliderItem";
    NoSliderClassName = NoSliderClassName.trim();
    
    return (
    <>

    {!this.state.isCarouselSupported && <NoSliderComp noOfItems = {this.props.NoSliderNoOfItems} 
      className={NoSliderClassName}
      padding={this.props.padding}
      >
        {this.props.children}
    </NoSliderComp>}

    {this.state.isCarouselSupported && <SliderStyled>
      {React.Children.map(this.props.children, (child, index) => (
        <SliderItem className={className}
        width={this.props.width?this.props.width:''}
        height={this.props.height?this.props.height:''}
        padding={this.props.padding ? this.props.padding : ''}
        >
         {child}
        </SliderItem>
      ))}
    </SliderStyled>}

    {this.props.viewall && 
    <FullWidthBtn className="FullWidthBtn theme-btn" margin="16px 16px 0" onClick={()=>this.onViewAllClick()}>{this.props.viewall}</FullWidthBtn>}
    
    </>);
  }
}

